.form__submit-button {
    width: 358px;
    height: 50px;
    transition: opacity 0.3s linear;
    cursor: pointer;
    border: 0;
    padding: 0;
    margin: 216px auto 0;
    background: #FFFFFF;
    border-radius: 2px;
    font-family: "Inter", Arial, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #000000;
    display: block;
  }
  
  .form__submit-button:hover {
    opacity: 0.8;
  }
  
  @media screen and (max-width: 767px) {
    .form__submit-button {
      width: 238px;
      height: 46px;
      font-size: 14px;
      line-height: 17px;
      margin: 13px auto 25px;
    }
  }