.element__button_delete {
    position: absolute;
    top: 20px;
    left: 244px;
    width: 18px;
    height: 19px;
    display: block;
    background-color: transparent;
    background-image: url(../../../../images/places/elements_delete.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 18px;
    transition: opacity 0.3s linear;
    cursor: pointer;
    border: none;
    padding: 0;
    margin: 0;
    z-index: 1;
  }
  
  .element__button_delete:hover {
    opacity: 0.6;
  }