.form__part {
    border: 0;
    width: 358px;
    box-sizing: border-box;
    font-family: "Inter", Arial, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    padding: 0;
    padding-bottom: 13px;
    border-bottom: 2px solid #CCCCCC;
    margin: 0;
    background: #000000;
  }
  
  @media screen and (max-width: 767px) {
    .form__part {
      width: 238px;
    }
  }